<template>
	<div id="app">
		<Nav v-if="shownav" />
		<transition name="fade" mode="out-in">
			<main class="it-main">
			<router-view :key="$route.fullPath"></router-view>
			</main>
		</transition>
		<Footer v-if="showfooter" />
	</div>
</template>

<script>
	import UIkit from 'uikit';
	import Icons from 'uikit/dist/js/uikit-icons';
	UIkit.use(Icons);

	import Nav from "./components/Nav.vue";
	import Footer from "./components/Footer.vue";

	export default {
		name: "App",
		mounted: function() {
			this.$store.dispatch("auth/findUserFromCookie")
			this.$store.dispatch("i18n/findLanguageFromCookie")
		},
		computed:{
			shownav(){
				return this.$route.name != 'viewer'
			},
			showfooter(){
				return this.$route.name != 'viewer'
			},
		},
		components: {
			Nav,
			Footer
		}
	};
</script>

