<template>
	<section v-if="author" id="it-author-profile" class="uk-section uk-section-muted">
		<div class="uk-container uk-container-xlarge">
			<div uk-grid>
				<aside class="uk-width-1-3@l uk-text-center">
					<article class="uk-card uk-card-body uk-card-default it-author-card">
						<figure class="it-author-avatar">
							<img :src="api_url + author.avatar.url" :alt="author.name" v-if="author.avatar" class="uk-border-rounded"/>
						</figure>

						<h1 class="it-author-name uk-margin-remove">{{ author.name }}</h1>
						<h2 class="uk-text-large uk-margin-remove-top">
							<span v-if="author.profession == 'Both'"><span uk-icon="image"></span> {{'AUTHOR_BOTH' | translate}}</span>
							<span v-if="author.profession == 'Writer'"><span uk-icon="pencil"></span> {{'AUTHOR_WRITER' | translate}}</span>
							<span v-if="author.profession == 'Drawer'"><span uk-icon="image"></span> {{'AUTHOR_DRAWER' | translate}}</span>
						</h2>
						
						<nav class="it-nav-social uk-flex uk-flex-middle uk-flex-center uk-margin-top">
							<a :href="author.author_instagram" target="_blank" v-if="author.author_instagram" uk-icon="icon: instagram" class="uk-button uk-button-default"></a>
							<a :href="author.author_twitter" target="_blank" v-if="author.author_twitter" uk-icon="icon: twitter" class="uk-button uk-button-default"></a>
							<a :href="author.author_facebook" target="_blank" v-if="author.author_facebook" uk-icon="icon: facebook" class="uk-button uk-button-default"></a>
							<a :href="author.author_web" target="_blank" v-if="author.author_web" uk-icon="icon: link" class="uk-button uk-button-default"></a>
						</nav>

						<nav class="it-author-actionbar uk-margin-medium-top">
							<a href="#" class="uk-button uk-button-secondary uk-border-rounded">{{'AUTHOR_HELP' | translate}}</a>
														
							<button v-if="author.follow_authors.length == 0" class="com-button-fav uk-button uk-button-dark uk-border-rounded addtofav" type="button" @click="addToFav">
								{{'AUTHOR_FOLLOW' | translate}}
							</button>

							<button v-else class="com-button-fav uk-button uk-button-dark uk-border-rounded removefromfav" type="button" @click="removeFromFav">
								{{'AUTHOR_UNFOLLOW' | translate}}
								
							</button>
							
						</nav>
						
					</article>
				</aside>

				<div class="com-author-library uk-width-2-3@l">
					<ul uk-tab="animation: uk-animation-fade">
						<li><a href="#">{{'AUTHOR_SERIES' | translate}}</a></li>
						<li><a href="#">Bio</a></li>
					</ul>

					<!-- This is the container of the content items -->
					<ul class="uk-switcher">
						<li>
							<h2>{{'AUTHOR_SERIES' | translate}}</h2>
							<SeriesList :series="author.series || []" ></SeriesList>
						</li>
						<li>
							<h2>Bio</h2>
							<div class="uk-margin-top uk-margin-bottom" v-if="author.bio">
								<vue-markdown-it :source="author.bio" id="editor" />
							</div>
						</li>
					</ul>
				</div>
			</div>
		</div>
	</section>
	<Loading v-else/>
</template>

<script>
	import SeriesList from "../components/SeriesList";
	import Loading from "../components/Loading.vue";
	import VueMarkdownIt from "vue-markdown-it";
	import { AUTHOR_ALL_AUTHORS } from "../constants/graphql";
	import strapi from "../../utils/Strapi";

	export default {
		mounted: function() {
			setTimeout(() => {
				this.id = this.$store.getters["auth/getId"];
				this.$apollo.queries.author.refetch();
			}, 100);
		},
		data() {
			return {
				api_url: process.env.VUE_APP_STRAPI_API_URL,
				author: null,
				routeParam: this.$route.params.id,
				id: null
			};
		},
		methods: {
			async addToFav() {
				await strapi
				.createEntry("follow-authors", {
					author: {
						id: this.author.id
					},
					user: {
						id: this.id
					}
				})
				.then(follow_author => {
					this.author.follow_authors.push(follow_author);
				})
				.catch(err => console.debug("err", err));
			},
			removeFromFav() {
				this.author.follow_authors.forEach(follow_author => {
					strapi
					.deleteEntry("follow-authors", follow_author.id)
					.then(() => {
						this.author.follow_authors = [];
					})
					.catch(err => console.debug("err", err));
				});
			}
		},
		components: {
			Loading,
			VueMarkdownIt,
			SeriesList
		},
		apollo: {
			author: {
				query: AUTHOR_ALL_AUTHORS,
				variables() {
					return { 
						id: this.routeParam,
						userId: this.id ?? 0					
					};
				}
			}
		}
	};
</script>