<template>
	<div class="uk-grid uk-child-width-1-2 uk-child-width-1-3@s uk-child-width-1-4@m uk-grid-small uk-grid-match" :class="classProp" uk-grid>
		<div v-for="serie in series" :key="serie.id">
			
			<router-link :to="{ path: '/serie/' + serie.id }" class="uk-link-reset">
				<div class="com-card-serie uk-card uk-card-small uk-background-cover uk-light" :data-src="api_url + serie.cover.url" data-sizes="(min-width: 650px) 650px, 100vw" uk-img>

					<div class=" uk-card-body">
						<p class="uk-card-badge uk-label" v-if="serie.is_finish">{{'COMPLETE' | translate}}</p>
						<p class="uk-card-badge uk-label" v-else>{{'ONGOING' | translate}}</p>
						<p class="uk-card-badge uk-label uk-label-success it-label-new" v-if="showNewBadge(serie)" >{{'NEW' | translate}}</p>
						<p v-if="serie.genres" class="it-serie-genre">
							<span v-for="genre in serie.genres" :key="genre.id">
								{{ genre.name }}
							</span>
						</p>
						<h3 class="uk-card-title uk-margin-remove-top uk-text-truncate" :title="serie.Title">{{ serie.Title }}</h3>
					</div>

				</div>
			</router-link>
		</div>
	</div>
</template>

<script>
	//import GenresList from "./GenresList.vue";

	var moment = require("moment");
	export default {
		name: "SeriesList",
		data: function() {
			return {
				api_url: process.env.VUE_APP_STRAPI_API_URL
			};
		},
		methods:{
			showNewBadge(serie){
				var updatedDate = moment(serie.updated_at);
				const diffDays = moment().diff(updatedDate, 'days');
				return diffDays <= 3;
			}
		},
		props: {
			series: Array,
			classProp: String,
		},
		components: {
			//GenresList
		},
	};
</script>